
html {
  font-size: 75px;
}
@media screen and (max-width: 320px) {
html {
      font-size: 64px;
}
}
@media screen and (min-width: 540px) {
html {
      font-size: 108px;
}
}
.m-news-content {
  width: 100vw;
  height: auto;
  display: block;
  overflow: hidden;
}
.news {
  width: 100vw;
  height: auto;
  display: block;
  overflow: hidden;
}
.load-more {
  width: 1.33333rem;
  height: 0.28rem;
  text-align: center;
  margin: 0.24rem auto;
  font-size: 0.2rem;
  font-family: PingFangSC-Medium;
  color: #A0A0A0;
}



