
* {
  margin: 0;
  padding: 0;
  font-family: "PingFangSC-Regular", "myfont", "Helvetica Neue", Helvetica, "Hiragino Sans GB", "STHeitiSC-Light", "Microsoft YaHei", "", Arial, sans-serif;
  -webkit-overflow-scrolling: touch;
}
body {
  width: 100%;
  height: 100%;
  max-width: 100vw;
}
.wrap .content {
  width: 100vw;
  height: 100%;
}
.wrap .content .time {
  width: 66.7vw;
  margin: 0 auto;
  text-align: left;
  font-size: 1vw;
  color: #A0A0A0;
}
.wrap .news-content {
  position: relative;
  top: 7.9vw;
}
.wrap .content .news-content .title {
  width: 66.7vw;
  margin: 0 auto 40px auto;
  font-size: 2vw;
  color: #3E3A39;
  font-family: PingFangSC-Semibold;
  font-weight: 700;
}
.wrap .content .footer {
  top: 7.9vw;
}
.wrap .news-content .text p {
  font-family: PingFangSC-Regular;
  font-size: 1vw;
  color: #3E3A39;
  width: 66.7vw;
  margin: 40px auto;
}
.wrap .news-content .text img {
  display: block;
  width: 66.7vw;
  margin: 0px auto;
}



