
html {
  font-size: 75px;
}
@media screen and (max-width: 320px) {
html {
      font-size: 64px;
}
}
@media screen and (min-width: 540px) {
html {
      font-size: 108px;
}
}
.news-detail .mobile-news-content {
  width: 100vw;
  height: auto;
  display: block;
  overflow: hidden;
}
.news-detail .mobile-news-content .title {
  width: 4.52rem;
  margin: 0.24rem auto 0.05333rem auto;
  font-family: PingFangSC-Semibold;
  font-weight: 700;
  font-size: 0.24rem;
  color: #3E3A39;
}
.news-detail .mobile-news-content .time {
  width: 4.52rem;
  height: 0.23333rem;
  margin: 0 auto;
  text-align: left;
  font-size: 0.17333rem;
  font-family: PingFangSC-Regular;
  color: #A0A0A0;
}
.news-detail .text {
  margin-top: 0.10667rem;
}
.news-detail .mobile-news-content .text p {
  width: 4.52rem;
  color: #3E3A39;
  margin: 0.24rem auto 0 auto;
  font-size: 0.2rem;
  line-height: 0.32rem;
  text-align: justify;
}
.news-detail .mobile-news-content .text img {
  width: 4.52rem;
  display: block;
  margin: 0.24rem auto 0.24rem auto;
}
.news-detail .foot {
  margin-top: 0.25333rem;
}



