  display: none;
}
a {
  color: #2D8cF0;
  background: transparent;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
}
a:hover {
    color: #8cc0f7;
}
a:active {
    color: #157fee;
}
a:active, a:hover {
    outline: 0;
    text-decoration: none;
}
a[disabled] {
    color: #ccc;
    cursor: not-allowed;
    pointer-events: none;
}
